<template>
  <div>
    <div class="stat__header">
      <div class="fl">商品类目分布
        <el-tooltip placement="right" effect="light">
          <div slot="content">只统计一级分类的类目所对应的商品数量</div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="14" class="stat__inner">
        <div id="catrgoryPie" class="line-chart"></div>
      </el-col>
      <el-col :span="10" class="border-left">
        <div class="table-box">
          <table class="stat-inner__table mt-20">
            <tr>
              <th>序号</th>
              <th>分类</th>
              <th>商品数量</th>
              <th>占比</th>
            </tr>
            <tr v-for="(item,index) in statisticsPage" :key="index">
              <td>{{item.id}}</td>
              <td>{{item.productCategory.cateName}}</td>
              <td>{{item.productCount}}</td>
              <td>{{Math.round(item.ratio*100)}}%</td>
            </tr>
          </table>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import {
  statisticsProductCategoryPieData,
  statisticsProductCategoryPage
} from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'catrgoryPie',
      optionsData: {
        title: {
          text: '商品类目分布',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          right: 'right',
          data: ['类目1', '类目2', '类目3']
        },
        series: [
          {
            name: '类目统计',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              { value: 1, name: '类目1' },
              { value: 1, name: '类目2' },
              { value: 1, name: '类目3' }
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      },
      newStatistics: {
        sumMemberCount: 0,
        sumNewMember: 0,
        sumRechargeMember: 0
      },
      statisticsPage: []
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsProductCategoryPieData(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            if (res.data.length > 0) {
              this.optionsData.legend.data = res.data.map(v => v.name)
              this.optionsData.series[0].data = res.data
            }
            this.chart.setOption(this.optionsData)
            this.getTableData()
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    getTableData() {
      statisticsProductCategoryPage(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.statisticsPage = res.data.content
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 350px;
}
.table-box{
  height: 390px;
  width: 100%;
  padding: 0 20px;
}
</style>



