<template>
  <div>
    <div class="stat__header">
      <div class="fl">商品概况
        <el-tooltip placement="right" effect="light">
          <div slot="content">上架商品数：正在出售的商品总数
            <br>被访问商品数：被访问的商品总数，一个商品多次访问计一次
            <br>动销商品数据：已出售的商品数据，不排除已退款退货的商品
            <br>商品浏览量：商品详情页被访问的次数
            <br>商品访客数：商品详情页访客数，一个用户访问多次计一次
            <br>加购件数：商品被加入购物车的数量
            <br>下单件数：商品被下单件数，不排除退货退款或未支付的订单
            <br>支付件数：商品支付的件数，不排除退货退款的订单
            <br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner"  style="padding-bottom:0px">
        <el-row class="stat-card__list" :gutter="20">
          <el-col :span="6">
            <div class="stat-card">
              <p>上架商品数</p>
              <count-to :endVal="newStatistics.shelfCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>被访问商品数</p>
              <count-to :endVal="newStatistics.accessCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>动销商品数</p>
              <count-to :endVal="newStatistics.purchasedCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>商品浏览量</p>
              <count-to :endVal="newStatistics.browseCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>商品访客数</p>
              <count-to :endVal="newStatistics.visitorsCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>加购件数</p>
              <count-to :endVal="newStatistics.addCartCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>下单件数</p>
              <count-to :endVal="newStatistics.orderCount" class="count-val"></count-to>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="stat-card">
              <p>支付件数</p>
              <count-to :endVal="newStatistics.orderPaySize" class="count-val"></count-to>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { getProductTotalInfo } from './../../../../api/statistics/index'
export default {
  mixins: [chartMixins],
  data() {
    return {
      newStatistics: {
        accessCount: 8,
        addCartCount: 26,
        browseCount: 2,
        id: null,
        orderCount: 216,
        orderPaySize: 216,
        purchasedCount: 216,
        shelfCount: 850,
        visitorsCount: 0
      }
    }
  },
  methods: {
    getData() {
      this.loading = true
      getProductTotalInfo(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.newStatistics = res.data
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 240px;
}
</style>



