<template>
<div class="stat__layout">
 <timer @change="getTime"></timer>
 <general class="mt-20" :time-data="value"></general>
 <category class="mt-20" :time-data="value"></category>
 <sale class="mt-20" :time-data="value"></sale>
</div>
</template>

<script>
 
import timer from './../components/time'
import general from './general'
import category from './category'
import sale from './sale'
export default {
   components: {
     general,
     category,
     sale,
     timer
   },
   data() {
     return {
       value: ['', '']
     }
   },
   methods: {
     getTime(time) {
       this.value = time
     }
   }
}
</script>

<style>
</style>



